@import "../_variables";
@import "./_form";

.addNewItem {
  padding-top: 10px;
  border-top: 1px solid $dark-grey;
  flex: 0 0 auto;
  overflow: auto;
  width: 100%;

  @extend form;
  form {
    label {
      &:not(:first-of-type) {
        margin-left: 5px;
      }
    }
  }
}
